<script>
import logo from "@/assets/images/logo.png";
import ewm from "@/assets/images/ewm.png";
import icon1 from "@/assets/images/icon1.jpg";
import icon2 from "@/assets/images/icon2.jpg";
import icon3 from "@/assets/images/icon3.jpg";
import icon4 from "@/assets/images/icon4.jpg";
import titlle1 from "@/assets/images/titlle1.png";
import titleImg from "@/assets/images/titleImg.png";
import MAC from "@/assets/images/MAC.png";
import lunbPic from "@/assets/images/lunbPic.png";
import lbBj from "@/assets/images/lbBj.png";
import hzjg from "@/assets/images/hzjg.png";
import dtdw from "@/assets/images/dtdw.jpg";
import Gallery from "../gallery";
export default {
  name: "demo",
  props: {
    value: {
      type: String
    }
  },
  components: { Gallery },
  data() {
    return {
      activeIndex: "0",
      activeClass: "listShow_active",
      errorClass: "listShow",
      activeClass1: "listShow_active1",
      errorClass1: "listShow1",
      activeClass2: "listShow_active2",
      errorClass2: "listShow2",
      isMenuFixed: false,
      getPageUrl: "static/testMsgWithIframe.html",
      images: [
        {
          url: lunbPic,
          descriptionsLB: {
            title: "  ",
            subtitle:
              "1060 "
          }
        },
        {
          url: MAC,
          descriptionsLB: {
            title: "  MCN",
            EN: "Program Overview",
            subtitle:
              "MCNMulti-Channel Network MCN  2017201812 500090%MCN MCN"
          }
        },

        {
          url: lbBj,
          descriptionsLB: {
            title: "  ",
            subtitle:
              ""
          }
        }
      ],
      dtdw,
      ewm,
      icon1,
      hzjg,
      icon2,
      icon3,
      icon4,
      titleImg,
      titlle1,
      logo,
      descriptions: {
        title: "",
        subtitle: "TAURUS VIDEO SHOW WEALTH DISPLAY INFLUENCE",
        description: ""
      },
      formData: {
        usrName: "",
        phoneNum: "",
        email: "",
        addr: "",
        remark: ""
      }
    };
  },
  computed: {},
  methods: {
    handleSelect(v) {
      const navBlock1St = document.getElementById("nav-block1").offsetTop;
      const navBlock2St = document.getElementById("nav-block2").offsetTop;
      const navBlock3St = document.getElementById("nav-block3").offsetTop;
      const navBlock4St = document.getElementById("nav-block4").offsetTop;
      const navBlock5St = document.getElementById("nav-block5").offsetTop;
      const activeIndexObj = {
        "1": navBlock1St,
        "2": navBlock2St,
        "3": navBlock3St,
        "4": navBlock4St,
        "5": navBlock5St
      };
      document.body.scrollTop = activeIndexObj[v] - 120;
      document.documentElement.scrollTop = activeIndexObj[v] - 120;
    },
    scrollActiveMemu() {
      const menuTop = document.getElementById("nav-menu").offsetTop + 200;
      const navBlock1St = document.getElementById("nav-block1").offsetTop - 100;
      const navBlock2St = document.getElementById("nav-block2").offsetTop - 200;
      const navBlock3St = document.getElementById("nav-block3").offsetTop - 200;
      const navBlock4St = document.getElementById("nav-block4").offsetTop - 200;
      const navBlock5St = document.getElementById("nav-block5").offsetTop - 200;
      let scrollTop =
        (document.documentElement.scrollTop ||
          window.pageYOffset ||
          document.body.scrollTop) + 120;
      if (scrollTop > menuTop) {
        this.isMenuFixed = true;
      } else {
        this.isMenuFixed = false;
        this.activeIndex = "0";
      }
      if (navBlock1St < scrollTop && scrollTop < navBlock2St) {
        this.activeIndex = "1";
      }
      if (navBlock2St < scrollTop && scrollTop < navBlock3St) {
        this.activeIndex = "2";
      }
      if (navBlock3St < scrollTop && scrollTop < navBlock4St) {
        this.activeIndex = "3";
      }
      if (navBlock4St < scrollTop && scrollTop < navBlock5St) {
        this.activeIndex = "4";
      }
      if (navBlock5St < scrollTop) {
        this.activeIndex = "5";
      }
    },
    submit() {
      if (this.checkout()) {
        //
        this.axios.post("/api/applyInfo/add", this.formData).then(res => {
          if (res.data) {
            this.$message({
              message: "",
              center: true,
              offset: 100,
              type: "success"
            });
            this.close();
          }
        });
      }
    },
    checkout() {
      if (!this.formData.usrName) {
        this.$message({
          message: "",
          center: true,
          offset: 100,
          type: "error"
        });
        return false;
      }
      if (!this.formData.phoneNum) {
        this.$message({
          message: "",
          center: true,
          offset: 100,
          type: "error"
        });
        return false;
      }
      return true;
    },
    close() {
      this.formData = {};
    }
  },
  watch: {},
  beforeCreate() {},
  cteated() {},
  beforeMount() {},
  mounted() {
    window.onscroll = this.scrollActiveMemu;
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {
    window.onscroll = null;
  }
};
</script>
